                      d="M10.9666 41.3C8.63328 38.7333 7.23328 35.9333 7.23328 31.2667C7.23328 23.1 13.0666 15.8667 21.2333 12.1333L23.3333 15.1667C15.6333 19.3667 13.9999 24.7333 13.5333 28.2333C14.6999 27.5333 16.3333 27.3 17.9666 27.5333C22.1666 28 25.4333 31.2667 25.4333 35.7C25.4333 37.8 24.4999 39.9 23.0999 41.5333C21.4666 43.1667 19.5999 43.8667 17.2666 43.8667C14.6999 43.8667 12.3666 42.7 10.9666 41.3ZM34.2999 41.3C31.9666 38.7333 30.5666 35.9333 30.5666 31.2667C30.5666 23.1 36.3999 15.8667 44.5666 12.1333L46.6666 15.1667C38.9666 19.3667 37.3333 24.7333 36.8666 28.2333C38.0333 27.5333 39.6666 27.3 41.2999 27.5333C45.4999 28 48.7666 31.2667 48.7666 35.7C48.7666 37.8 47.8333 39.9 46.4333 41.5333C45.0333 43.1667 42.9333 43.8667 40.5999 43.8667C38.0333 43.8667 35.6999 42.7 34.2999 41.3Z"
