                </v-card-title>
                <v-card-title style="font-size: 20px; font-weight: 700"
                  >د. أحمد عباس سندي</v-card-title
                >
                <v-card-text
                  class="text py-5"
                  style="font-size: 16px; font-weight: 400; line-height: normal"
                >
                  الحمد لله رب العالمين، وصلاةً وسلاماً على المبعوث رحمةً
                  للعالمين نبينا محمد بن عبدالله وعلى آله وصحبه أجمعين ، لقد
                  منَّ الله – تعالى- على هذهِ البلادِ المباركة بأن شرفها بخدمة
                  ضيوف الرحمن من حجاج البيت العتيق، فاصطفى لها ولاة أَمْرٍ –
                  أيدهم الله- دستورهم كِتابَ الله وسنة رسوله الكريم – صلى الله
                  عليه وسلم-، انصب اهتمامهم على العناية بضيوف الرحمن وتوجيه كافة
                  الجهات المعنية من القطاعات الحكومية والأهلية لتسخير كافة
                  الإمكانيات لراحتهم والعناية بهم وتلبية احتياجاتهم لأداء
                  مناسكهم بكل يسر وسهولة واطمئنان وأمن وأمان ، وليس أدل على ذلك
                  من تلك المشاريع العملاقة للحرمين الشريفين والمشاعر المقدسة
                  لتستقبل أعداد الحجاج الكرام المتزايد عاماً بعد عام، فلهم منا
                  أسمى آيات الثناء والشكر والتقدير..
                </v-card-text>
              </div>
              <div class="img">
                <img src="@/assets/images/احمد-عباس.svg" alt="" />
              </div>
            </div>
            <v-card-text
              class="py-5"
              style="font-size: 16px; font-weight: 400; line-height: normal"
            >
              وقد أصاب “ابن خلدون” حين قال: “إنَّ التاريخَ في ظاهرِهِ لا يزيد عن
              الإخبار، ولكنْ في باطنه نظر، وتحقيق”، وهو ما ينطبق على سيرة شركة
              مطوفي حجاج الدول الأفريقية غير العربية التي تأسست في عام 1442هـ،
              ولكنها ليست نبتًا من العدم، بل هي امتدادٌ لتاريخ مؤسسة عريقة
              (مؤسسة مطوفي حجاج الدول الأفريقية غير العربية) بُنيت منذ عام
              1404هـ ، واستلهاماً من تاريخ المؤسسة الناصع في خدمة ضيوف الرحمن من
              حجاج البيت الحرام، تسير شركتنا بخطى واثقة وعزم أكيد في طريق تقديم
              أفضل وأسمى الخدمات لضيوف الرحمن في ظل رعاية وتوجيه خادم الحرمين
              الشريفين الملك سلمان بن عبد العزيز – حفظه الله- قائد مسيرتنا وراعي
              نهضتنا، ومعه عضده وسنده صاحب السمو الملكي الأمير محمد بن سلمان بن
              عبد العزيز ولي العهد رئيس مجلس الوزراء – حفظه الله- صاحب الكاريزما
              المضيئة وقائد الرؤية الفذة “رؤية 2030” التي تنقل بلادنا العزيزة
              إلى آفاق أرحب من التقدم والرقي والرفاه بفضل الله ، وانطلاقاً من
              “رؤية 2030″
            </v-card-text>
            <v-card-text
              class="py-5"
              style="font-size: 16px; font-weight: 400; line-height: normal"
            >
              جاء التحول المؤسسي لشركات أرباب الطوائف – وفي القلب منها شركة
              “أفريقيا غير العربية”- بالنزوع إلى التوجه التجاري الاستثماري
              والعمل بأسلوب الشركات الحديثة بغية الوصول بخدمة ضيوف الرحمن وصناعة
              الضيافة إلى أعلى المراتب.. من ناحية، وإيجاد عوائد مالية مستدامة
              لشركتنا ولمساهميها ومساهماتها من أبناء الجمعية العامة على مدار
              العام وليس في مواسم الحج فقط.. من ناحية أخرى ، وقد تجسد ما سبق في
              مجموعة من الخطوات والانجازات لشركتنا في الآونة الأخيرة، ومنها على
              سبيل المثال – لا الحصر- إنشاء شركة أفريقيا غير العربية القابضة
              للاستثمار، والتي انبثق منها شركتان جديدتان هما: إثراء الجود لخدمات
              الحجاج، وإثراء الخير لخدمات الحجاج. كما وقعت الشركة عقداً مميزاً
              مع شركة البداد للمناسك (شركة عالمية) لمشروع تطوير خيام “أفريقيا
              غير العربية” بمشعر عرفات، وذلك على مساحة إجمالية تبلغ (246) ألف
              متر مربع.
            </v-card-text>
            <v-card-text
              class="py-5"
              style="font-size: 16px; font-weight: 400; line-height: normal"
              >كذلك، وقعت الشركة عقداً تستحوذ بمقتضاه على (51%) من شركة “عبد
              الله سعيد أبو الجدايل للصناعة”، وهو ما يتيح للشركة دخول مجال خدمات
              الإعاشة من الباب الواسع ، وما سبق غيض من فيض، ومجرد بداية لما هو
              قادم من أفكار وخطط ومشروعات يقف خلفها فريق من المخلصين الراغبين في
              النجاح والتفوق قوامه أعضاء مجلس الإدارة، وكافة منسوبي الشركة،
              مدفوعين بدعم من الجمعية العامة من المساهمين والمساهمات، وهو ما
              تجلى في الجمعية العامة غير العادية المنعقدة في يوم 18 مارس 2023م
              بمقر الغرفة التجارية بمكة المكرمة.</v-card-text
            >
            <v-card-text
              class="py-5"
              style="font-size: 16px; font-weight: 400; line-height: normal"
              >وأخيراً.. أتقدم – بالأصالة عن نفسي وبالنيابة عن أعضاء مجلس
              الإدارة وكافة مساهمي ومساهمات الشركة ومنسوبيها- بأسمى آيات الشكر
              والتقدير لقيادة المملكة الرشيدة – أيدها الله- على دعمها اللامحدود
              وتوجيهاتها السديدة للجميع بتقديم أفضل الخدمات لضيوف الرحمن. والشكر
              موصول لمقام وزارة الحج والعمرة بقيادة صاحب المعالي الوزير د. توفيق
              الربيعة، ومعالي نائبه د. عبد الفتاح مشاط، ولكافة وكلاء الوزارة
              ومنسوبيها، على دعمهم الكبير لشركتنا لأداء دورها في خدمة الحجيج على
              أكمل وجه.</v-card-text
            >
          </v-card>
